import React from "react";
import { Link } from "react-router-dom";

const NavBar = (props) => {
    return (
        <nav className={`navbar navbar-expand-lg navbar-${props.Theme} bg-${props.Theme} position-fixed w-100`} style={{zIndex: "999"}}>
            <div className="container-fluid">
                <Link className="navbar-brand" to="/">
                    NewsMonkey
                </Link>
                <button
                    className="navbar-toggler"
                    type="button"
                    data-bs-toggle="collapse"
                    data-bs-target="#navbarSupportedContent"
                    aria-controls="navbarSupportedContent"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                >
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div
                    className="collapse navbar-collapse"
                    id="navbarSupportedContent"
                >
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link
                                className="nav-link"
                                aria-current="page"
                                to="/"
                            >
                                Home
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/business">
                                Business
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/entertainment">
                                Entertainment
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/sports">
                                Sports
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/science">
                                Science
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/health">
                                Health
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/technology">
                                Technology
                            </Link>
                        </li>
                    </ul>
                    <div className={`form-check form-switch text-${props.Theme === "dark" ? "light" : "dark"} nav-item my-auto`}>
                        <input className="form-check-input" onClick={props.toggleTheme} type="checkbox" id="flexSwitchCheckChecked" />
                        <label className="form-check-label" htmlFor="flexSwitchCheckChecked">Disable Light Mode</label>
                    </div>
                </div>
            </div>
        </nav>
    );
};

export default NavBar;