import React, { Component } from "react";
import PropTypes from "prop-types";
import NewsItem from "./NewsItem";
import Spinner from "./Spinner";
import CryptoTable from "./CryptoTable";
import WeatherCard from "./WeatherCard";
import InfiniteScroll from "react-infinite-scroll-component";
import "./component.css"

export default class News extends Component {
    static defaultProps = {
        country: "in",
        pageSize: 6,
        category: "general",
    };

    static propTypes = {
        country: PropTypes.string,
        pageSize: PropTypes.number,
        category: PropTypes.string,
    };
    constructor() {
        super();
        this.state = {
            articles: [],
            loading: true,
            page: 1,
            totalResults: 0,
            matches: window.matchMedia("(min-width: 768px)").matches
        };
    }

    fetchMoreData = async () => {
        this.setState({ loading: true, page: this.state.page + 1 });
        let url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&page=${this.state.page}&pageSize=${this.props.pageSize}&category=${this.props.category}&apiKey=${this.props.apiKey}`;
        let data = await fetch(url);
        let parsedData = await data.json();
        this.setState({
            articles: this.state.articles.concat(parsedData.articles),
            loading: false,
            totalResults: parsedData.totalResults,
        });
    };

    updateData = async () => {
        this.setState({ loading: true });
        let url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&page=${this.state.page}&pageSize=${this.props.pageSize}&category=${this.props.category}&apiKey=${this.props.apiKey}`;
        let data = await fetch(url);
        let parsedData = await data.json();
        this.setState({
            articles: parsedData.articles,
            loading: false,
            totalResults: parsedData.totalResults,
        });
        document.title = `${this.props.category === "general" ? "Home" : this.props.category} - NewsMonkey`;
    };

    async componentDidMount() {
        this.updateData();
        const handler = e => this.setState({ matches: e.matches });
        window.matchMedia("(min-width: 960px)").addListener(handler);
    }

    render() {
        return (
            <div className={`container-fluid p-0 text-${this.props.Theme === "dark" ? "light" : "dark"} row mx-0`} style={{marginTop: "4em"}}>
                <h1 className="text-left mb-3">NewsMonkey - Top {this.props.category === "general" ? "" : this.props.category.charAt(0).toUpperCase() + this.props.category.slice(1)} Headlines</h1>
                <InfiniteScroll
                    dataLength={this.state.articles.length}
                    next={this.fetchMoreData}
                    hasMore={this.state.articles.length !== this.state.totalResults}
                    loader={<Spinner />}
                >
                    <div className="row">
                        <div className="newsDiv col-xl-8 col-sm-12 row">
                            {this.state.articles.map((element) => {
                                return (
                                        <div
                                            className="my-1 mx-auto"
                                            key={element.url}
                                        >
                                            <NewsItem
                                                title={element.title ? element.title.slice(0, 91) + (element.title.length < 91 ? "" : "...") : "Sorry, Title not available right now."}
                                                description={element.description ? element.description : "Sorry, Description not available right now."}
                                                imgUrl={element.urlToImage ? element.urlToImage : "https://www.macworld.com/wp-content/uploads/2021/03/itunesguylarg-100001724-orig-112.jpg"}
                                                newsUrl={element.url}
                                                author={element.author}
                                                sourceName={element.source.name}
                                                Theme={this.props.Theme}
                                            />
                                        </div>
                                );
                            })}
                        </div>
                        <div className={"sideDiv col-xl-4"}>
                            <WeatherCard Theme={this.props.Theme}/>
                            <CryptoTable Theme={this.props.Theme}/>
                            <div className="fakeAds h-100"><h1>Advertisments</h1></div>
                        </div>
                    </div>
                </InfiniteScroll>
            </div >
        );
    }
}